@import 'normalize';
@import 'fonts';
@import 'slick';
@import 'slick-theme';

/* 	Общие стили -- Начало 	*/
*, *:after, *:before {
	box-sizing: border-box;
}

a {
	text-decoration: none;
}

body {
	font-family: 'Exo 2', sans-serif;
	overflow-x: hidden;
}

.container {
	max-width: 1440px;
	margin: 0 auto;
	padding: 0 30px;
}
/* 	Общие стили -- Конец 	*/


/* 	Общие настройки для разделов -- Начало 	*/
.section {
	padding: 60px 0;
	&__title {
		font-weight: 700;
		font-size: 48px;
		text-align: center;
		line-height: 1.2;
		margin: 0;
		margin-bottom: 40px;
	}
}
/* 	Общие настройки для разделов -- Конец 	*/




/* 	Раздел "Начальный экран -- Начало 	*/
.header {
	height: 100vh;
	background-image: linear-gradient(63deg, #390661, #a15aa4);
	color: #fff;
	position: relative;
	z-index: 10;
	&-title {
		font-size: 78px;
		font-weight: 700;
		margin-top: 120px;
		letter-spacing: 2px;
		text-shadow: 0 0 5px black;
		max-width: 60%;
	}
	&-subtitle {
		display: block;
		font-size: 36px;
	}
	&-description {
		max-width: 50%;
		font-size: 22px;
		line-height: 1.6;
		text-shadow: 0 0 5px black;
	}
	img {
		position: absolute;
		top: 0%;
		right: 0%;
		height: 100%;
		max-width: 100%;
		z-index: -1;
		object-fit: cover;
	}
}

.navbar {
	padding: 15px 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-width: 70%;
	&-logo {
		font-weight: 700;
		font-size: 26px;
	}
	&-link {
		font-size: 10px;
		transition: 0.2s;
		a {
			font-size: 18px;
			display: block;
			color: #fff;
			position: relative;
			transition: all 0.2s;
			&:before {
				position: absolute;
				content: '';
				left: 0;	
				bottom: 0;
				width: 0;
				height: 1px;
				background-color: #ffffff;
			}
			&:hover:before {
				width: 100%;
				left: 0;
			}
		} 
	}
}
/* 	Раздел "Начальный экран -- Конец 	*/


/* 	Раздел "Наша фишки -- Начало 	*/
.features {
	position: relative;
	&-block {
		display: flex;
		justify-content: space-around;
		flex-wrap: wrap;
		&__item {
			text-align: center;
			flex-basis: 25%;
			&-img {
				margin-bottom: 20px;
				svg {
					width: 100px;
					height: 100px;
				}
			}
		}
	}
}
/* 	Раздел "Наша фишки" -- Конец 	*/



/* 	Раздел "Интересные факты" -- Начало 	*/
.facts {
	background: #dcdcdc;
	position: relative;
	.container {
		position: relative;
	}
	height: auto;
	.arrow {
		border: none;
		background-color: transparent;
		cursor: pointer;
		margin: 0;
		padding: 0;
		position: absolute;
		outline: none;
		top: 50%;
		transform: translateY(-50%);
		transition: .2s;
		&-prev {
			left: 10%;
		}
		&-next {
			right: 10%;
		}
	}
}

.slider {
	font-family: 'caveat';
	font-size: 32px;
	margin: 0 auto;
	width: 60%;
	display: flex;
	-webkit-align-items: center;
	-moz-align-items: center;
	-ms-align-items: center;
	-o-align-items: center;
	align-items: center;
	height: 100%;
	&-item {
		text-align: center;
	}
}
/* 	Раздел "Интересные факты" -- Конецу 	*/




/* 	Раздел "Каталог" -- Начало 	*/
.catalog {
	position: relative;
	&-block {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly;
	}
}

.card {
	flex-basis: 23%;
	height: 250px;
	border: 1px solid rgba(0,0,0, .4);
	transition: .2s;
	margin-bottom: 20px;
	transition: .2s;
	cursor: pointer;
	&__img {
		height: 200px;
	}
	&__title {
		height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	img {
		object-fit: cover;
		width: 100%;
		height: 100%;
	}
	&:hover {
		box-shadow: 0 0 10px rgba(0,0,0, .4);
	}
}
/* 	Раздел "Каталог" -- Конец	*/


.parallax {
	position: absolute;
}

.flower {
	&-2 {
		right: -3%;
		bottom: -20%;
	}
	&-3 {
		left: -10%;
		bottom: -10%;
	}
}



/* 	Раздел "Уход за цветком" -- Начало 	*/
.care {
	background: #dcdcdc;
	&-block {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		&__item {
			flex-basis: 48%;
			min-width: 33%;
			display: flex;
			justify-content: space-between;	
			margin-bottom: 20px;
			&-info {
				h3 {
					margin-top: 0;
				}
				p {
					line-height: 1.3;
				}
			}
			&-img { 
				margin-right: 10px;	
				img {
					width: 120px;
					height: 120px;
				}
			}
		}
	}
}
/* 	Раздел "Уход за цветком" -- Конец 	*/




/* 	Подвал сайта -- Начало 	*/
.footer {
	background-color: #333333;
	.container {
		padding: 20px 0;
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		.author {
			color: #fff;
			transition: 0.2s;
			&:hover {
				border-bottom: 1px dashed #fff;
			}
		}
		.social-item {
			margin-right: 15px;
			&__vk {
				svg {
					&:hover {
						fill: #45668e;
					}
				}
			}
			&__instagram {
				svg {
					&:hover {
						fill: #e1306c;
					}
				}
			}
			&__telegram {
				svg {
					&:hover {
						fill: #0088cc;
					}
				}
			}
			svg {
				height: 32px;
				width: 32px;
				fill: #fff;
			}
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
/* Подвал сайта -- Конец */

@import "media";