/* exo-2-200 - latin_cyrillic */
@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-weight: 200;
  font-display: swap;
  src: url('../fonts/exo2/exo-2-v6-latin_cyrillic-200.eot'); /* IE9 Compat Modes */
  src: local('Exo 2 Extra Light'), local('Exo2-ExtraLight'),
       url('../fonts/exo2/exo-2-v6-latin_cyrillic-200.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/exo2/exo-2-v6-latin_cyrillic-200.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/exo2/exo-2-v6-latin_cyrillic-200.woff') format('woff'), /* Modern Browsers */
       url('../fonts/exo2/exo-2-v6-latin_cyrillic-200.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/exo2/exo-2-v6-latin_cyrillic-200.svg#Exo2') format('svg'); /* Legacy iOS */
}
