}

// Medium devices (tablets, less than 992px)
@media (max-width: 991.98px) { 
  
}

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {  
  .section {
    &__title {
      font-size: 40px;
    }
  }
  .header {
    &-title {
      font-size: 58px;
      max-width: 100%;
    }
    &-subtitle {
      font-size: 28px;
    }
    &-description {
      max-width: 100%;
    }
  }
  .navbar {
    max-width: 100%;
  }
  .features {
    &-block {
      justify-content: center;
      &__title {
        font-size: 14px;
      }
    }
    }
  .card {
    flex-basis: 30%;
  }
  .care {
    &-block {
      &__item {
        flex-basis: 100%;
        min-width: 100%;
        &-img {
          img {
            width: 100px;
            height: 100px;
          }
        }
      }
    }
  }
  .parallax {
    display: none;
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) { 
  .header {
    &-title {
      font-size: 40px;
    }
    &-subtitle {
      font-size: 22px;
    }
    &-description {
      font-size: 18px;
    }
  }
  .navbar {
    &-logo {
      font-size: 22px;
    }
    &-link a {
      font-size: 16px;
    }
  }
  .features {
    &-block {
      justify-content: center;
      &__title {
        font-size: 14px;
      }
      &__item {
        flex-basis: 50%;
        margin-bottom: 20px;
      }
    }
  }
  .slider {
    font-size: 26px;
  }
  .arrow {
    img {
      width: 32px;
      height: 32px;
    }
  }
  .card {
    flex-basis: 48%;
  }
}