/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("./../img/ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("../fonts/slick.eot");
  src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff") format("woff"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}

/* 	Общие стили -- Начало 	*/
*, *:after, *:before {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

body {
  font-family: 'Exo 2', sans-serif;
  overflow-x: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 30px;
}

/* 	Общие стили -- Конец 	*/
/* 	Общие настройки для разделов -- Начало 	*/
.section {
  padding: 60px 0;
}

.section__title {
  font-weight: 700;
  font-size: 48px;
  text-align: center;
  line-height: 1.2;
  margin: 0;
  margin-bottom: 40px;
}

/* 	Общие настройки для разделов -- Конец 	*/
/* 	Раздел "Начальный экран -- Начало 	*/
.header {
  height: 100vh;
  background-image: linear-gradient(63deg, #390661, #a15aa4);
  color: #fff;
  position: relative;
  z-index: 10;
}

.header-title {
  font-size: 78px;
  font-weight: 700;
  margin-top: 120px;
  letter-spacing: 2px;
  text-shadow: 0 0 5px black;
  max-width: 60%;
}

.header-subtitle {
  display: block;
  font-size: 36px;
}

.header-description {
  max-width: 50%;
  font-size: 22px;
  line-height: 1.6;
  text-shadow: 0 0 5px black;
}

.header img {
  position: absolute;
  top: 0%;
  right: 0%;
  height: 100%;
  max-width: 100%;
  z-index: -1;
  object-fit: cover;
}

.navbar {
  padding: 15px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 70%;
}

.navbar-logo {
  font-weight: 700;
  font-size: 26px;
}

.navbar-link {
  font-size: 10px;
  transition: 0.2s;
}

.navbar-link a {
  font-size: 18px;
  display: block;
  color: #fff;
  position: relative;
  transition: all 0.2s;
}

.navbar-link a:before {
  position: absolute;
  content: '';
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background-color: #ffffff;
}

.navbar-link a:hover:before {
  width: 100%;
  left: 0;
}

/* 	Раздел "Начальный экран -- Конец 	*/
/* 	Раздел "Наша фишки -- Начало 	*/
.features {
  position: relative;
}

.features-block {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.features-block__item {
  text-align: center;
  flex-basis: 25%;
}

.features-block__item-img {
  margin-bottom: 20px;
}

.features-block__item-img svg {
  width: 100px;
  height: 100px;
}

/* 	Раздел "Наша фишки" -- Конец 	*/
/* 	Раздел "Интересные факты" -- Начало 	*/
.facts {
  background: #dcdcdc;
  position: relative;
  height: auto;
}

.facts .container {
  position: relative;
}

.facts .arrow {
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin: 0;
  padding: 0;
  position: absolute;
  outline: none;
  top: 50%;
  transform: translateY(-50%);
  transition: .2s;
}

.facts .arrow-prev {
  left: 10%;
}

.facts .arrow-next {
  right: 10%;
}

.slider {
  font-family: 'caveat';
  font-size: 32px;
  margin: 0 auto;
  width: 60%;
  display: flex;
  -webkit-align-items: center;
  -moz-align-items: center;
  -ms-align-items: center;
  -o-align-items: center;
  align-items: center;
  height: 100%;
}

.slider-item {
  text-align: center;
}

/* 	Раздел "Интересные факты" -- Конецу 	*/
/* 	Раздел "Каталог" -- Начало 	*/
.catalog {
  position: relative;
}

.catalog-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card {
  flex-basis: 23%;
  height: 250px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  transition: .2s;
  margin-bottom: 20px;
  transition: .2s;
  cursor: pointer;
}

.card__img {
  height: 200px;
}

.card__title {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.card:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

/* 	Раздел "Каталог" -- Конец	*/
.parallax {
  position: absolute;
}

.flower-2 {
  right: -3%;
  bottom: -20%;
}

.flower-3 {
  left: -10%;
  bottom: -10%;
}

/* 	Раздел "Уход за цветком" -- Начало 	*/
.care {
  background: #dcdcdc;
}

.care-block {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.care-block__item {
  flex-basis: 48%;
  min-width: 33%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.care-block__item-info h3 {
  margin-top: 0;
}

.care-block__item-info p {
  line-height: 1.3;
}

.care-block__item-img {
  margin-right: 10px;
}

.care-block__item-img img {
  width: 120px;
  height: 120px;
}

/* 	Раздел "Уход за цветком" -- Конец 	*/
/* 	Подвал сайта -- Начало 	*/
.footer {
  background-color: #333333;
}

.footer .container {
  padding: 20px 0;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.footer .container .author {
  color: #fff;
  transition: 0.2s;
}

.footer .container .author:hover {
  border-bottom: 1px dashed #fff;
}

.footer .container .social-item {
  margin-right: 15px;
}

.footer .container .social-item__vk svg:hover {
  fill: #45668e;
}

.footer .container .social-item__instagram svg:hover {
  fill: #e1306c;
}

.footer .container .social-item__telegram svg:hover {
  fill: #0088cc;
}

.footer .container .social-item svg {
  height: 32px;
  width: 32px;
  fill: #fff;
}

.footer .container .social-item:last-child {
  margin-right: 0;
}

/* Подвал сайта -- Конец */
@media (max-width: 767.98px) {
  .section__title {
    font-size: 40px;
  }
  .header-title {
    font-size: 58px;
    max-width: 100%;
  }
  .header-subtitle {
    font-size: 28px;
  }
  .header-description {
    max-width: 100%;
  }
  .navbar {
    max-width: 100%;
  }
  .features-block {
    justify-content: center;
  }
  .features-block__title {
    font-size: 14px;
  }
  .card {
    flex-basis: 30%;
  }
  .care-block__item {
    flex-basis: 100%;
    min-width: 100%;
  }
  .care-block__item-img img {
    width: 100px;
    height: 100px;
  }
  .parallax {
    display: none;
  }
}

@media (max-width: 575.98px) {
  .header-title {
    font-size: 40px;
  }
  .header-subtitle {
    font-size: 22px;
  }
  .header-description {
    font-size: 18px;
  }
  .navbar-logo {
    font-size: 22px;
  }
  .navbar-link a {
    font-size: 16px;
  }
  .features-block {
    justify-content: center;
  }
  .features-block__title {
    font-size: 14px;
  }
  .features-block__item {
    flex-basis: 50%;
    margin-bottom: 20px;
  }
  .slider {
    font-size: 26px;
  }
  .arrow img {
    width: 32px;
    height: 32px;
  }
  .card {
    flex-basis: 48%;
  }
}
